import { boundMethod } from 'autobind-decorator';
import TreeNode from 'model/node/TreeNode';
import React from 'react';
import BaseEvent from 'store/manager/event/wrapper/BaseEvent';
import isMac from 'util/event/isMac';

/**
 * EventHandler   keyboard event type .
 * React event  wrapper .
 */
export default class KeyEvent extends BaseEvent {
  /**
   *    react key event
   */
  private readonly event: React.KeyboardEvent<HTMLElement>;

  /**
   * 
   *
   * @param event react key event .
   * @param targetModel event  TreeNode
   */
  public constructor(event: React.KeyboardEvent<HTMLElement>, eventTargetModel: TreeNode) {
    super(eventTargetModel);
    this.event = event;
  }

  /**
   * Mac  option,   alt   alt key    .
   *
   * @returns alt key    true
   */
  @boundMethod
  public isAltDown(): boolean {
    return this.event.altKey;
  }

  /**
   * Mac  control,   ctrl   ctrl key    .
   *
   * @returns ctrl key    true
   */
  @boundMethod
  public isCtrlDown(): boolean {
    return this.event.ctrlKey;
  }

  /**
   * shift    .
   *
   * @returns shift    true
   */
  @boundMethod
  public isShiftDown(): boolean {
    return this.event.shiftKey;
  }

  /**
   * Mac  command, Windows  windows   meta key    .
   *
   * @returns meta key    true
   */
  @boundMethod
  public isMetaDown(): boolean {
    return this.event.metaKey;
  }

  /**
   * Modifier    .
   * Mac  modifier  command,  ctrl  modifier .
   *
   * @returns modifier    true
   */
  @boundMethod
  public isModifierDown(): boolean {
    return isMac()
      ? this.event.metaKey && !this.event.altKey
      : this.event.ctrlKey && !this.event.altKey;
  }

  /**
   * CapsLock    .
   *
   * @returns CapsLock   true
   */
  @boundMethod
  public isCapsLockDown(): boolean {
    return this.event.getModifierState('CapsLock');
  }

  /**
   * ScrollLock     .
   *
   * @returns ScrollLock   true
   */
  @boundMethod
  public isScrollLockDown(): boolean {
    return this.event.getModifierState('ScrollLock');
  }

  /**
   * NumLock     .
   *
   * @returns NumLock   true
   */
  @boundMethod
  public isNumLockDown(): boolean {
    return this.event.getModifierState('NumLock');
  }

  /**
   * event key .
   *
   * @returns event.key
   */
  @boundMethod
  public getKey(): string {
    return this.event.key;
  }

  /**
   * event code  . (Key code  deprecated .)
   *
   * @returns key code  string
   */
  @boundMethod
  public getCode(): string {
    return this.event.nativeEvent.code;
  }

  /**
   * react event .
   *
   * @returns this.event
   */
  @boundMethod
  protected getEvent(): React.SyntheticEvent<HTMLElement> {
    return this.event;
  }

  /**
   *     .
   *
   * @returns     
   */
  public getIsComposing(): boolean {
    return (this.getNativeEvent() as KeyboardEvent).isComposing;
  }
}
