import { boundMethod } from 'autobind-decorator';
import CommandCompositor from 'store/manager/command/CommandCompositor';

/**
 * Command  stack   class .
 * undoable  commandCompositor    undo stack  .
 * Stack  ,  deque  .
 * Command   push  stack size   FIFO  commandCompositor .
 */
export default class UndoStack {
  /**
   * Undo stack .   commandCompositor  .
   */
  private undoStack: CommandCompositor[];

  /**
   * Undo  command   redo stack . Command execute  clear .
   */
  private redoStack: CommandCompositor[];

  /**
   * Undo stack  max size  .
   */
  private readonly size: number;

  /**
   * 
   *
   * @param size undoStack 
   */
  public constructor(size = 20) {
    this.undoStack = [];
    this.redoStack = [];
    this.size = size;
  }

  /**
   * Undo    .
   *
   * @returns Undo stack  command    true
   */
  @boundMethod
  public canUndo(): boolean {
    return this.undoStack.length > 0;
  }

  /**
   * Redo    .
   *
   * @returns Redo stack  command    true
   */
  @boundMethod
  public canRedo(): boolean {
    return this.redoStack.length > 0;
  }

  /**
   *   commandCompositor  undo stack  .
   * Redo stack  clear , undo stack size    FIFO  ..
   *
   * @param commandCompositor   commandCompositor
   */
  @boundMethod
  public push(commandCompositor: CommandCompositor): void {
    this.redoStack = [];
    this.undoStack.push(commandCompositor);
    if (this.undoStack.length > this.size) {
      this.undoStack.shift();
    }
  }

  /**
   * Undo   CommandCompositor  redo stack   .
   *
   * @returns Undo   CommandCompositor, undo stack   undefined
   */
  @boundMethod
  public prev(): Nullable<CommandCompositor> {
    const command = this.undoStack.pop();
    if (command === undefined) {
      return undefined;
    }
    this.redoStack.push(command);
    return command;
  }

  /**
   * Redo   CommandCompositor  undo stack    .
   *
   * @returns Redo   CommandCompositor, redo stack   undefined
   */
  @boundMethod
  public next(): Nullable<CommandCompositor> {
    const command = this.redoStack.pop();
    if (command === undefined) {
      return undefined;
    }
    this.undoStack.push(command);
    return command;
  }
}
