import { boundMethod } from 'autobind-decorator';

/**
 * node id  container.
 */
class IdGenerator {
  /**
   * IdContainer  id    
   */
  private id;

  /**
   * 
   */
  constructor() {
    this.id = 0;
  }

  /**
   * id .
   *
   * @returns     id
   */
  @boundMethod
  public generateId(): number {
    // eslint-disable-next-line no-plusplus
    return this.id++;
  }
}

export default new IdGenerator();
