/**
 *  Path ,  util  .
 */

/**
 * Z segment .
 *
 * @returns Z segment
 */
export function makeClose(): string {
  return `Z `;
}

/**
 * M segment .
 *
 * @returns M segment
 */
export function makeMove(x: number, y: number): string {
  return `M ${x} ${y} `;
}

/**
 * L segment .
 *
 * @returns L segment
 */
export function makeLine(x: number, y: number): string {
  return `L ${x} ${y} `;
}
