import { boundMethod } from 'autobind-decorator';
import FillBase from 'model/node/graphic/info/fill/FillBase';
import { FillTypeEnum } from 'types/model/node/graphic/FillTypeEnum';

/**
 *     class .
 * fillBase   .
 */
export default class FillInfo {
  /**
   * Fill        
   */
  private fill: Nullable<FillBase>;

  /**
   * 
   */
  constructor() {
    this.fill = undefined;
  }

  /**
   * fill .
   *
   * @param fill    fill
   */
  @boundMethod
  public setFill(fill: Nullable<FillBase>): void {
    this.fill = fill;
  }

  /**
   * FillInfo  fill .
   *
   * @returns FillInfo  fill
   */
  @boundMethod
  public getFill(): Nullable<FillBase> {
    return this.fill;
  }

  /**
   *   fill type .
   *
   * @returns fill type
   */
  public getFillType(): Nullable<FillTypeEnum> {
    return this.fill?.getType();
  }

  /**
   *  class   .
   * @returns  FillInfo
   */
  public clone(): FillInfo {
    const newFillInfo = new FillInfo();
    newFillInfo.setFill(this.fill?.clone());
    return newFillInfo;
  }
}
