import { boundMethod } from 'autobind-decorator';
import AppContext from 'store/context/AppContext';
import CommandCompositor from 'store/manager/command/CommandCompositor';
import CommandController from 'store/manager/command/CommandController';
import CommandMapper from 'store/manager/command/CommandMapper/CommandMapper';
import CommandModeMapper from 'store/manager/command/CommandModeMapper';
import { CommandEnum } from 'types/store/command/CommandEnum';
import { ICommandProps } from 'types/store/command/CommandTypes';

/**
 * Command   class .
 *  tree node    Command manager  .
 */
export default class CommandManager {
  /**
   *   (command mode)  CommandMapper List   Mapper .
   */
  private commandModeMapper: CommandModeMapper;

  /**
   * 
   */
  public constructor() {
    this.commandModeMapper = new CommandModeMapper();
  }

  /**
   * command   command handler    command mapper .
   *
   * @param ctx  app    AppContext
   * @returns command mode  command mapper
   */
  @boundMethod
  private getCommandMapper(ctx: AppContext): Nullable<CommandMapper> {
    return this.commandModeMapper.get(ctx.getEditableContext().getCommandMode());
  }

  /**
   * command compositor  .
   *
   * @param ctx  app    AppContext
   * @returns    CommandCompositor
   */
  private createCommandCompositor(ctx: AppContext): CommandCompositor {
    return new CommandCompositor(ctx);
  }

  /**
   * command controller  .
   *
   * @param commandCompositor CommandController   command compositor
   * @returns    CommandController
   */
  private createCommandController(commandCompositor: CommandCompositor): CommandController {
    return new CommandController(commandCompositor);
  }

  /**
   * CommandId  command  .
   *
   * @param ctx  app    AppContext
   * @returns command     
   */
  @boundMethod
  public execute(ctx: AppContext): boolean {
    const editableContext = ctx.getEditableContext();
    const commandProps = editableContext.getCommandProps();
    if (commandProps === undefined) {
      return false;
    }

    const { commandId } = commandProps;
    if (commandId === CommandEnum.UNDO) {
      return this.executeUndo(ctx, commandProps);
    }
    if (commandId === CommandEnum.REDO) {
      return this.executeRedo(ctx, commandProps);
    }

    return this.executeCurrentCommand(ctx, commandProps);
  }

  /**
   *    command  .
   *
   * @param ctx  app    AppContext
   * @param commandProps command     CommandProps
   * @returns  command   
   */
  @boundMethod
  private executeCurrentCommand(ctx: AppContext, commandProps: ICommandProps): boolean {
    const editableContext = ctx.getEditableContext();
    // compose simple commands
    const commandController = this.createCommandController(this.createCommandCompositor(ctx));
    editableContext.setCommandController(commandController);

    let isCommandValid = true;

    this.getCommandMapper(ctx)
      ?.get(commandProps.commandId)
      ?.some(commandHandler => {
        if (isCommandValid === false) {
          return true;
        }

        // simple command  CommandController  CommandCompositor  append  
        const response = commandHandler.processCommand(ctx, commandProps);
        if (response.isValid === false) {
          isCommandValid = false;
          commandController.fallback();
          return true;
        }

        return response.terminate;
      });

    if (isCommandValid === true) {
      commandController.executeCommand();
      commandController.executePostCommand();
      commandController.finishApplyProcess(ctx);

      return true;
    }

    return false;
  }

  /**
   *    Undo  .
   *
   * @param ctx  app    AppContext
   * @param commandProps command     CommandProps
   * @returns  command   
   */
  @boundMethod
  private executeUndo(ctx: AppContext, commandProps: ICommandProps): boolean {
    const editableContext = ctx.getEditableContext();
    // compose simple commands
    const commandController = this.createCommandController(this.createCommandCompositor(ctx));
    commandController.unExecuteCommand(ctx);
    return true;
  }

  /**
   *    Redo  .
   *
   * @param ctx  app    AppContext
   * @param commandProps command     CommandProps
   * @returns  command   
   */
  @boundMethod
  private executeRedo(ctx: AppContext, commandProps: ICommandProps): boolean {
    const editableContext = ctx.getEditableContext();
    // compose simple commands
    const commandController = this.createCommandController(this.createCommandCompositor(ctx));
    commandController.reExecuteCommand(ctx);
    return true;
  }
}
