/**
 * store     .
 *  simple command     , undo/redo  .
 */
abstract class SimpleCommand {
  /**
   * Simple command  do .
   */
  public abstract apply(): void;

  /**
   * Simple command  undo .
   */
  public abstract unapply(): void;

  /**
   * Simple command  redo .
   */
  public abstract reapply(): void;
}

export default SimpleCommand;
