import AppContext from 'store/context/AppContext';
import KeyEvent from 'store/manager/event/wrapper/KeyEvent';
import MouseEvent from 'store/manager/event/wrapper/MouseEvent';
import WheelEvent from 'store/manager/event/wrapper/WheelEvent';
import Identifiable from 'util/id/Identifiable';

/**
 * event   event handler class .
 * event    event handler chain  ,
 * event     .
 * chain   event handler event   true ,
 *  chain   event chain .
 */
class EventHandler {
  /**
   * mouse down event    .
   *
   * @param event  event
   * @param ctx  app    AppContext
   * @returns event    
   */
  public onMouseDown(event: MouseEvent, ctx: AppContext): boolean {
    return false;
  }

  /**
   * mouse up event    .
   *
   * @param event  event
   * @param ctx  app    AppContext
   * @returns event    
   */
  public onMouseUp(event: MouseEvent, ctx: AppContext): boolean {
    return false;
  }

  /**
   * mouse move event    .
   *
   * @param event  event
   * @param ctx  app    AppContext
   * @returns event    
   */
  public onMouseMove(event: MouseEvent, ctx: AppContext): boolean {
    return false;
  }

  /**
   * mouse drag event    .
   *
   * @param event  event
   * @param ctx  app    AppContext
   * @returns event    
   */
  public onDrag(event: MouseEvent, ctx: AppContext): boolean {
    return false;
  }

  /**
   * mouse wheel event    .
   *
   * @param event  event
   * @param ctx  app    AppContext
   * @returns event    
   */
  public onWheel(event: WheelEvent, ctx: AppContext): boolean {
    return false;
  }

  /**
   * key down event    .
   *
   * @param event  event
   * @param ctx  app    AppContext
   * @returns event    
   */
  public onKeyDown(event: KeyEvent, ctx: AppContext): boolean {
    return false;
  }

  /**
   * key up event    .
   *
   * @param event  event
   * @param ctx  app    AppContext
   * @returns event    
   */
  public onKeyUp(event: KeyEvent, ctx: AppContext): boolean {
    return false;
  }

  public onAppAreaMouseDown(event: MouseEvent, ctx: AppContext): boolean {
    return false;
  }

  public onAppAreaMouseUp(event: MouseEvent, ctx: AppContext): boolean {
    return false;
  }

  public onAppAreaMouseMove(event: MouseEvent, ctx: AppContext): boolean {
    return false;
  }

  public onAppAreaDrag(event: MouseEvent, ctx: AppContext): boolean {
    return false;
  }
}

export default class extends Identifiable(EventHandler) {}
